import React from 'react';
import PropTypes from 'prop-types';

import { withStyles } from '../tools/withStyles';
import { Link } from '../components/Link';
import { Main } from '../components/Main';
import { Text } from '../components/Text';
import { Fader } from '../components/Fader';
import { Secuence } from '../components/Secuence';

const styles = theme => ({
  root: {}
});

class Creators extends React.Component {
  static propTypes = {
    classes: PropTypes.object
  };

  render () {
    const { classes } = this.props;

    return (
      <Main className={classes.root}>
        <article>
          <Secuence stagger>
            <header>
            <h1 style={{textAlign:"center"}}><Text>Creators</Text></h1>
            </header>
            <p style={{textAlign:"center"}}><b>
              <Text>
              "Escape competition through authenticity"
            </Text>
            </b>
            </p>
            <p style={{textAlign:"center"}}>
              Meet the heroes who possess the superpower to mind-control the flow of billions of electrons in a microchip
            </p>
            <div className="container" style={{display: 'flex', textAlign: "center"}}>
                <div>
                  <h3 style={{textAlign:"center"}}>Technoking</h3>
                  <Fader style={{ display: 'block' }}>
                    <a href='https://www.twitter.com/saurabhchalke' target="_blank">
                      <img
                        src='/images/saurabh.jpg'
                        style={{ width: '90%', maxWidth: '400px' }}
                      />
                    </a>
                    <h3 style={{textAlign:"center"}}>Saurabh Chalke</h3>
                    <p style={{textAlign:"center"}} target="_blank"> 
                      <a href='https://www.twitter.com/saurabhchalke'>
                          <span className='mdi mdi-twitter' />
                      </a></p>
                  </Fader>
                </div>
                <div>
                  <h3 style={{textAlign:"center"}}>Technocrat</h3>
                  <Fader style={{ display: 'block' }}>
                      <a href='https://www.linkedin.com/in/anvesh-garg' target="_blank">
                        <img
                          src='/images/anvesh.jpg'
                          style={{ width: '90%', maxWidth: '400px' }}
                        />
                      </a>
                    <h3 style={{textAlign:"center"}}>Anvesh Garg</h3>
                    <p style={{textAlign:"center"}}> 
                      <a href='https://www.linkedin.com/in/anvesh-garg' target="_blank">
                          <span className='mdi mdi-linkedin' />
                      </a></p>
                  </Fader>
                </div>
              </div>
          </Secuence>
        </article>
      </Main>
    );
  }
}

export default withStyles(styles)(Creators);
